import React from 'react';
import ContentLoader from "react-content-loader";
import InfiniteScroll from 'react-infinite-scroller';
import TimeAgo from 'react-timeago';
import { switchLoader } from '../../commonRedux/';
import { profilePic } from '../../globalFunctions';
import { connectSocket, friendRequest } from '../../hooks/socket';
import { acceptRequest, cancelRequest, followUser } from '../../http/http-calls';
import { getNotifications, setNotificationsViewed } from '../../http/post-calls';
import { history } from '../../store';

require ("./styles.scss");

class AllNotifications extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            page:1,
            limit: 20,
            users: [],
            seemore:false,
            showSkeleton: true
        };
    } 
    getSuggestedFriends = () => {
     
        getNotifications({limit: this.state.limit, page: this.state.page}).then(resp => {
            console.log(resp);
            this.setState({ users: [...this.state.users, ...resp.notifications], showSkeleton: false }, () => {
                this.checkMore(1);
            });
        }, error => {
            console.log(error);
        });
     
    }
    checkMore = (i) => {
        getNotifications({ limit: this.state.limit, page: this.state.page+1 }, true)
        .then(async resp => {
           if(resp.notifications.length > 0){
               this.setState({
                   seemore: true
               })
           }else{
            this.setState({
                seemore: false
            })
           }
        }, error => {
            
        });
    }
    goToNotification = (item) => {
        setNotificationsViewed();
        this.setState({
          new:0
        }, ()=>{
         
        if(item.type == 'friend_request' ||  item.type == 'request_accepted'){
          history.push("/u/"+item.userId);
        }
        else {
          history.push("/post/"+item.data.postid);
        }
        })
        
      }
    componentDidMount() {
        this.getSuggestedFriends();
        if(this.props.currentUser != undefined){
            connectSocket(this.props.currentUser._id);
        }
        friendRequest((request) => {
            window.location.reload();
        });
    }

    addFriend = (id, userIndex) => {
        switchLoader(true, 'Following...! ');
        followUser({ followerid: id }, true)
          .then(async resp => {
            switchLoader();
            let tempUser = this.state.users;
            tempUser[userIndex]._id = resp._id;
            tempUser[userIndex].request = resp.request;
            tempUser[userIndex].type = "sent";
            if(this.props.refresh != null){
                this.props.refresh();
            }
            this.setState({
                users: tempUser
            })
          }, error => {
            switchLoader();
            
          });
      }

    //   callPendingRequests = () => {
    //     getPendingRequests({}, true)
    //       .then(async resp => {
    //         resp.length == 0 && setShowSkeletonPR(false) ;
    //       }, error => {
    //         switchLoader();
            
    //       });
    //   }
    
    //   callSuggestions = () => {
    //     getSuggestions({ limit: 10, page: 1 }, true)
    //       .then(async resp => {
    //         resp.message.length == 0 && setShowSkeleton(false);
    //         switchLoader();
    //       }, error => {
    //         switchLoader();
            
    //       });
    //   }
    

    
      accept = (user, index, item) => {
        switchLoader(true, 'Following...! ');
        if(item == 0){
            acceptRequest({ id: user._id }, true)
            .then(async resp => {
            switchLoader();
            let tempUser = this.state.users;
            delete tempUser[index];
            if(this.props.refresh != null){
                this.props.refresh();
            }
            this.setState({
                users: tempUser
            })
          }, error => {
            switchLoader();
          });
        }else{
            cancelRequest({id: user.uid}, true)
            .then(async resp => {
              switchLoader();
              let tempUser = this.state.users;
              // tempUser[index] = null;
              delete tempUser[index].request;
              if(this.props.refresh != null){
                  this.props.refresh();
              }
              this.setState({
                  users: tempUser
              })
            }, error => {
              switchLoader();
            });
        }
      }
    
      cancelRequest = (id,index, item) => {
        switchLoader(true, 'Cancelling...! ');
        cancelRequest({id}, true)
          .then(async resp => {
            switchLoader();
            let tempUser = this.state.users;
            // tempUser[index] = null;
            delete tempUser[index].request;
            if(this.props.refresh != null){
                this.props.refresh();
            }
            this.setState({
                users: tempUser
            })
          }, error => {
            switchLoader();
          });
      }
      loadFunc = () => {
        if (!this.state.showSkeleton) {
            this.setState({ showSkeleton: true })
            this.setState(prevState => {
                return {
                    ...prevState,
                    page: prevState.page + 1
                }
            }, () => {
                this.getSuggestedFriends();
            })
        }

    }

    render() {
        return (
            <div className="seeAllPage">
                <div className="container my-wall-container ">
                <div className="row mt-2">
                    <div className="col-sm empty-container-with-out-border left-column">
                    </div>
                    <div className="col-sm empty-container-with-out-border center-column">
                        <div className="empty-container-with-border w-100 popularArticles">
                            <h3 className="title border-bottom pb-2">Notifications</h3>
                            {
                            this.state.users.length === 0 && this.state.toggleSkeleton && <p className="no-found">No Data Found</p> 
                            }
                            <InfiniteScroll
                                pageStart={0}
                                initialLoad={false}
                                loadMore={this.loadFunc}
                                hasMore={this.state.seemore}
                            >
                                <ul className="list-group w-100">
                      
                      {
                        this.state.users && this.state.users.map((not, i) => {
                          return <li className="list-group-item pointer" key={i}>
                          <div  onClick={() => this.goToNotification(not)} className={"media w-100 d-flex align-items-center "+(!not.viewed ? 'active' : '')}>
                            <div className="media-left">
                              <span >
                                <img className="media-object pic circle"
                                  src={profilePic(not.user.avatar)} alt="..." />
                              </span>
                            </div>
                            <div className="media-body">
                            <p className="media-heading">
                              { not.type == 'friend_request' && <span><b>{not.user.name}</b> sent you a friend request</span> }
                              { not.type == 'request_accepted' && <span><b>{not.user.name}</b> has accepted your request</span> }
                              { not.type == 'post_liked' && <span><b>{not.user.name}</b> liked your post</span> }
                              { not.type == 'post_commented' && <span><b>{not.user.name}</b> commented  on your post</span> }
                              { not.type == 'comment_liked' && <span><b>{not.user.name}</b> liked  your comment</span> }
                              { not.type == 'comment_replied' && <span><b>{not.user.name}</b> replied to  your comment</span> }
                              { not.type == 'post_tagged' && <span><b>{not.user.name}</b> tagged you in a post</span> }
                              - <TimeAgo date={not.createdAt} /></p>
                            </div>
                          </div>
                        </li>
                        })
                      }
                   
                      
                    </ul>
                        
                            </InfiniteScroll>
                        

                        {(this.state.showSkeleton) &&
                            <ul className="list-group w-100">
                                <li className="list-group-item">
                                    {Array(3)
                                        .fill()
                                        .map((item, index) => (
                                            <ContentLoader
                                                speed={2}
                                                height={40}
                                                viewBox="0 0 200 40"
                                                backgroundColor="#f3f3f3"
                                                foregroundColor="#ecebeb"
                                                {...this.props}
                                                key={index}
                                            >
                                                <rect x="48" y="8" rx="3" ry="3" width="88" height="6" />
                                                <rect x="48" y="26" rx="3" ry="3" width="52" height="6" />
                                                <circle cx="20" cy="20" r="20" />
                                            </ContentLoader>
                                        ))}
                                </li>
                            </ul>
                        }
                        
                        </div>
                    </div>
                    {/* <!-- end center column --> */}

                    {/* <!--  right column --> */}
                    <div className="col-sm empty-container-with-out-border right-column">
                    </div>
                    {/* <!-- end right column --> */}
                </div>
                </div>
            </div>

      )
    }
}

export default AllNotifications;
